@font-face {
    font-family: 'DM-Sans'; 
    src: url('../Fonts/DM_Sans/DMSans-font.ttf') format('truetype'); 
    
  }

  
:root{
    /** colors */
    --body-color:#FDFEFF;
    --text-color: #353839;
    --server-message-color: inherit;
    --label-color: inherit;
    --scrap-button-color: #3A67C5 /*#1e83e9*/;
    --scrap-button-text-color: #FDFEFF;
    --add-button-text-color: #3A67C5;
    --disabled-button-color: #6f737a;
    --tooltip-background: #333;

    /** font */
    --font-family: "DM-Sans", "Arial";
    --server-message-font-size: clamp(28px, 6rem, 32px);
    --input-font-size: clamp(16px, 1.8rem, 18px);
    --label-font-size: clamp(14px, 1.5rem, 16px);
    --submit-btn-font-size: clamp(18px, 2rem, 20px);
    --add-btn-font-size: clamp(14px, 1.3rem, 16px);
    --description-font-size: clamp(12px, 1.3rem, 14px);
    --url-font-size: clamp(10px, 12pt, 14px);
    


    /** dimensions */
    --server-message-mobile-font-size: clamp(18px, 2rem, 22px);
    --main-containr-min-width: 33vw;
    --main-container-max-width: 35vw;
    --label-width: 6rem;
    --inputs-list-div-max-height: 30dvh;

    /** shapes */
    --scrap-btn-broder-radis: 5px;

    /** positioning */
    --main-space-from-top: 10dvh;


}

body{
    font-family: var(--font-family);
    background-color: var(--body-color);
    color: var(--text-color);
}

#root{
    width: 100%;
    height: 100dvh;
    display: flex;
    align-items: center;
    justify-content: center;
    flex: column;
}

.app{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: flex-start;
    min-width: 100%;
    max-width: 100%;
    min-height: 100%;
    max-height: 100%;
}


button{
    cursor: pointer;
}

button:disabled, button[disabled]{
    cursor: not-allowed;
}

.main_app_container{
    display: flex;
    flex-direction: column;
    justify-content: center;
    min-width: var(--main-containr-min-width);
    max-width: var(--main-container-max-width);
    max-height: 60%;

    margin-top: var(--main-space-from-top);
}



#server_message{
    color: var(--server-message-color);
    font-size: var(--server-message-font-size);
    text-align: center;
    font-weight: 500;
    cursor: default;
    max-width: 100%;
    max-height: 25%;
}

#description{
    text-align: center;
    font-size: var(--description-font-size);
    font-weight: 400;
    max-width: 100%;
    max-height: 10%;
    margin: 0.8rem 0;
}

#field{
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin: 1.2rem 0 0 0;
    transition: 0.3s ease-in;
    max-width: 100%;
    max-height: 25%;
}

.inputs_list{
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    max-height: var(--inputs-list-div-max-height);
    overflow-y: auto;
    position: relative;
    scrollbar-color: var(--submit-button-color);
    margin-top: 2dvh;
}

.inputs_list::-webkit-scrollbar{
    width: 6px;
}
.inputs_list::-webkit-scrollbar-track{
    background: transparent;
}
.inputs_list::-webkit-scrollbar-thumb{
    background: #C9C9C9;
    border-radius: 20px;
}

.label{
    color: var(--label-color);
    /*width: var(--label-width);*/
    padding: 0;
    margin-left: 0.5rem;
    font-size: var(--label-font-size);
    font-weight: bold;
    
    position: absolute;
    transform: translateX(1rem);
    cursor: text;
    pointer-events: none;

    transition: 0.3s ease;

    opacity: 80%;

}

.input{
    font-family: var(--font-family);
    font-size: var(--input-font-size);
    margin: 0.2rem 0.5rem;
    padding: 0.5rem;
    transition: all 0.8s ease;
    width: 100rem;
}

.input:focus{
    transition: none;
    outline-color: var(--scrap-button-color);
}

.input:not(:placeholder-shown) + .label{
    transform: translateY(-2rem);
    opacity: 100%;
}

#field:hover >.input:enabled, #field:hover >.input[enabled]{
    width: 88rem;
}

#field:hover > .del-btn:enabled, #field:hover > .del-btn[enabled],
.input:focus + .label + .del-btn:enabled, .input:focus + .label + .del-btn[enabled]{
    opacity: 100%;
    position: relative;
    transition: 0.2s ease;
}












/** Smaller Screen */

@media screen and (max-width: 800px) {

    .main_app_container{
        max-width: 95%;
    }


    #server_message{
        font-size: var(--server-message-mobile-font-size);
        /*margin-top: 80%;*/
    }

}